// CrimeCard.js

import React, { Component } from 'react';
import '../../css/CrimeCard.css';
import {baseUrl} from "../../App";
import ModalQuery from "../ModalQuery";
class CrimeCard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isEditing: false,
            message:"",
            crimeType: this.props.crimeCard.crimeType,
            editedCrimeData: {
                victimName: this.props.crimeCard.victimName,
                criminalName: this.props.crimeCard.criminalName,
                placeOfCrime: this.props.crimeCard.placeOfCrime,
                weapon: this.props.crimeCard.weapon,
                crimeTime: this.props.crimeCard.crimeTime,
                crimeType: this.props.crimeCard.crimeType,
                visionUrl: this.props.crimeCard.visionUrl,
                confirmationVisible: false
            },
        };
    }

    handleEditClick = () => {
        this.setState({ isEditing: true });
    };

    handleChangeStatus = (event) => {
        const newStatus = event.target.value;
        const confirmation = window.confirm(` ,      ${newStatus}?`);

        if (confirmation) {
            this.state.editedCrimeData.crimeType = newStatus
            this.setState({crimeType:newStatus}, ()=>{console.log(this.state.editedCrimeData.crimeType)})
        } else {
            this.setState({ newStatus: '' });
        }
    };

    handleInputChange = (e) => {
        const { role } = this.props
        if (role==="DETECTIVE")
        {
        const { name, value } = e.target;
        this.setState((prevState) => ({
            editedCrimeData: {
                ...prevState.editedCrimeData,
                [name]: value,
            },
        }))}
        else
        {   const { value } = e.target;
            this.setState({message:value})}
    };

    handleSaveClick = () => {
        const token = localStorage.getItem('jwtToken');
        const { role } = this.props
        if (role==="DETECTIVE") {
            const url = `${baseUrl}/api/v1/cards/${this.props.crimeCard.id}`;

            fetch(url, {
                method: 'PUT',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(this.state.editedCrimeData),
            })
                .then(response => {
                    if (!response.ok) {
                        throw new Error('   ');
                    }
                    console.log('  ');
                    this.props.onRenew();
                    this.props.onClose();
                    this.setState({isEditing: false});
                })
                .catch(error => {
                    this.setState({confirmationVisible:true})
                });



        }
        else
        {
            const FormData = require('form-data');
            const form = new FormData();
            form.append('message', this.state.message);
            console.log(this.state.message)
            fetch(`${baseUrl}/api/v1/auditor/cards/${this.props.crimeCard.id}`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                },
                body: form,
            })
                .then(response => {
                    if (!response.ok) {
                        //     2xx (),  
                        throw new Error(`HTTP error! Status: ${response.status}`);
                    }

                    //  response.text(),      JSON
                    return response.text();
                })
                .then(data => {
                    console.log(data);
                    this.props.onRenew();
                })
                .catch(error => {
                    console.error('    :', error);
                });
            this.props.onClose();
            this.setState({isEditing: false});
        }

    };

    closeQuery = (e) =>{
        this.setState({confirmationVisible:false})
    }


    render() {
        const { crimeCard, onClose, role } = this.props;
        const { isEditing, editedCrimeData, message, confirmationVisible} = this.state;

        return (
            <div className="crime-card">
                <div className="crime-card-content">
                    <span className="close" onClick={onClose}>
                        &times;
                    </span>
                    {isEditing && (role==="AUDITOR") &&
                        ( <div>

                            <h2 className="h-style">Message</h2>
                            <table className="bg-rg">
                                <tbody>
                                <tr>

                                    <td className="table-label-edit">Text message:</td>

                                <td className="table-label-edit">

                                    <textarea
                                    maxLength="2000"
                                    className="message"
                                    type="text"
                                    name="message"
                                    value={message}
                                    onChange={this.handleInputChange}
                                />
                                </td>

                                </tr>
                                </tbody>
                            </table>
                            <button className="button-edit" onClick={this.handleSaveClick}>Send message</button>
                                </div>)}
                    {isEditing && (role==="DETECTIVE") && ( <div>

                            <h2 className="h-style">Edit Card #{crimeCard.id}</h2>
                        <table className="bg-rg">
                            <tbody>
                            <tr>

                                <td className="table-label-edit-left-out">Victim Name:</td>
                                <td className="table-label-edit-left"><input
                                    maxLength="20"
                                    type="text"
                                    name="victimName"
                                    value={editedCrimeData.victimName}
                                    onChange={this.handleInputChange}
                                /></td>
                            </tr>
                            <tr>

                                <td className="table-label-edit-left-out">Criminal Name:</td>
                                <td className="table-label-edit-left"><input
                                    maxLength="20"
                                    type="text"
                                    name="criminalName"
                                    value={editedCrimeData.criminalName}
                                    onChange={this.handleInputChange}
                                /></td>
                            </tr>
                            <tr>

                                <td className="table-label-edit-left-out">Place of Crime:</td>
                                <td className="table-label-edit-left"><input
                                    maxLength="40"
                                    type="text"
                                    name="placeOfCrime"
                                    value={editedCrimeData.placeOfCrime}
                                    onChange={this.handleInputChange}
                                /></td>
                            </tr>
                            <tr>

                                <td className="table-label-edit-left-out">Weapon:</td>
                                <td className="table-label-edit-left"><input
                                    maxLength="20"
                                    type="text"
                                    name="weapon"
                                    value={editedCrimeData.weapon}
                                    onChange={this.handleInputChange}
                                /></td>
                            </tr>
                            <tr>

                                <td className="table-label-edit-left-out">Type Crime:</td>
                                <td className="table-label-edit-left">
                                    <select value={this.state.crimeType} onChange={this.handleChangeStatus}>
                                        <option className="table-select" value="INTENTIONAL" name="crimeType">INTENTIONAL</option>
                                        <option className="table-select" value="UNINTENTIONAL" name="crimeType">UNINTENTIONAL</option>
                                    </select>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                            <button className="button-edit" onClick={this.handleSaveClick}>Save</button>
                        </div>)

                    }
                    {!isEditing &&
                        <div>
                            <h2 className="h-style">Card #{crimeCard.id}</h2>
                            <table className="bg-rg">
                                <tbody>
                                <tr>

                            <td className="table-label-edit-left-out">Victim Name:</td>
                            <td className="table-label-edit-left">{crimeCard.victimName}</td>
                                    </tr>
                                <tr>
                            <td className="table-label-edit-left-out">Criminal Name: </td>
                            <td className="table-label-edit-left">{crimeCard.criminalName}</td>
                                </tr>
                                <tr>
                            <td className="table-label-edit-left-out">Place of Crime:</td>
                            <td className="table-label-edit-left">{crimeCard.placeOfCrime}</td>
                                </tr>
                                    <tr>
                                        <td className="table-label-edit-left-out">Weapon:</td>
                                        <td className="table-label-edit-left">{crimeCard.weapon}</td>
                                    </tr>
                                <tr>
                                    <td className="table-label-edit-left-out">Crime Time:</td>
                                    <td className="table-label-edit-left">{crimeCard.crimeTime}</td>
                                </tr>
                                <tr>
                                    <td className="table-label-edit-left-out">Responsible Detective:</td>
                                    <td className="table-label-edit-left">{crimeCard.responsibleDetective}</td>
                                </tr>
                                <tr>
                                    <td className="table-label-edit-left-out">Is Criminal Caught:</td>
                                    <td className="table-label-edit-left">{crimeCard.isCriminalCaught ? 'Yes':'No'}</td>
                                </tr>
                                <tr>
                                    <td className="table-label-edit-left-out">Crime Type:</td>
                                    <td className="table-label-edit-left">{crimeCard.crimeType}</td>
                                </tr>
                                <tr>
                                    <td className="table-label-edit-left-out">Vision URL:</td>
                                    <td className="table-label-pr">
                                        <div className="video-container-card">
                                            {crimeCard.visionUrl && (

                                                <iframe
                                                    title="Vision Video"
                                                    src={crimeCard.visionUrl}
                                                    frameBorder="0"
                                                    allowFullScreen
                                                ></iframe>)}
                                        </div></td>
                                </tr>
                            </tbody>
                        </table>
                            <button className="button-edit" onClick={this.handleEditClick}>
                                {role==="DETECTIVE" ? 'Edit':'Report a mistake'}
                            </button>

                        </div>
                    }
                </div>
                {confirmationVisible && (
                    <ModalQuery updateQuery={this.closeQuery}
                                query="Input all fields"
                                confirm = {false}
                    />
                )}
            </div>
        );
    }
}

export default CrimeCard;
