import React, { Component } from 'react';
import "../../css/Profile.css"
import fuel from "../../img/fuel.png"
import retire from "../../img/retire-tr.png"
import close from "../../img/close.png"
import rehab from "../../img/rehab.png"
import Refuel from "./Refuel";
import RegTransport from "./RegTransport";
import {baseUrl} from "../../App";
import ModalQuery from "../ModalQuery";
class TableTransport extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedRow: null,
            showModal_2: false,
            showModal: false,
            refuel: false,
            carList: null,
            confirmationVisible:false,
            action:null,
            query:'',
            sortBrand:false,
            sortModel:false,
            brand:false,
            model:false
        };
    }

    queryAnswer = (answer) => {
        this.setState({confirmationVisible:false}, ()=>{
            answer === 'Yes' ? this.state.action() : (
                answer === 'No' ? console.log('OK') : console.log('OK')
            )
        })
    }


    openModal = () => {
        this.setState({ showModal: true });
    };

    closeModal = () => {
        this.setState({showModal: false});
        this.props.renew();
    };

    openModal_2 = () => {
        this.setState({ showModal_2: true });
        this.setState({ refuel: true });
    };

    closeModal_2 = () => {
        this.setState({ showModal_2: false});
        this.setState({ refuel: false });
        this.props.renew();
    };

    componentDidMount() {
        this.fetchTransport()
    }

    fetchTransport = () => {
        const token = localStorage.getItem('jwtToken');

        fetch(`${baseUrl}/api/v1/reactiongroup/transport`, {
            method: 'GET', //   
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`,
            },
        })
            .then(responses => responses.json())
            .then(data => {
                this.setState({carList:data})
                console.log(this.state.carList)
            })
            .catch(error => {
                console.error('    :', error);
                this.setState({carList:null})
            });
        this.setState({action:null})
    }

    del = () => {
        const token = localStorage.getItem('jwtToken');
        const url = `${baseUrl}/api/v1/reactiongroup/transport/${this.state.selectedRow}`;
      //  const confirmation = window.confirm(`Are you sure delete this transport?`);

        {
            fetch(url, {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`,
                },
            })
                .then(response => {
                    if (!response.ok) {
                        //     2xx (),  
                        throw new Error(response.status);
                    }
                    this.fetchTransport()
                })
                .catch(error => {
                    if (error.message ==="404")
                        window.alert("You don't choose position");
                })};

    };

    retire = () => {
        const token = localStorage.getItem('jwtToken');
        const putData = {
            id: this.state.idTransport,
        };
        //const confirmation = window.confirm(`Are you sure retire this transport?`);

         {
             fetch(`${baseUrl}/api/v1/reactiongroup/transport/${this.state.selectedRow}/retire`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`,
                },
                body: JSON.stringify(putData),
            })
                .then(response => {
                    if (!response.ok) {
                        //     2xx (),  
                        throw new Error(response.status);
                    }
                    this.fetchTransport()
                })
                .catch(error => {
                    if (error.message ==="404")
                        window.alert("You don't choose position");
                })};
        this.fetchTransport()
    };

    rehab = () => {
        const token = localStorage.getItem('jwtToken');
        const putData = {
            id: this.state.idTransport,
        };
        //const confirmation = window.confirm(`Are you sure rehabilitate this transport?`);

         {
             fetch(`${baseUrl}/api/v1/reactiongroup/transport/${this.state.selectedRow}/rehabilitate`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`,
                },
                body: JSON.stringify(putData),
            })
                .then(response => {
                    if (!response.ok) {
                        //     2xx (),  
                        throw new Error(response.status);
                    }
                    this.fetchTransport()
                })
                .catch(error => {
                    if (error.message ==="404")
                        window.alert("You don't choose position");
                })};
        this.fetchTransport()
    };

    handleAction = (carId, actionFunction, textQuery) => {
        this.setState({query:textQuery},()=>{
            this.setState({confirmationVisible:true})
            })
        if (carId !== this.state.selectedRow) {
            this.setState({ selectedRow: carId }, () => {
                console.log(this.state.selectedRow);
                this.setState({action:actionFunction})
            });
        } else {
            this.setState({action:actionFunction})
        }
    }

    sortData = (field) => {
        const isAscending = this.state[field] || false;
        console.log(this.state.carList)
        if (this.state.carList){
        const sortedList = this.state.carList.sort((a, b) => {
            const valueA = typeof a[field] === 'string' ? a[field] : a[field].toString();
            const valueB = typeof b[field] === 'string' ? b[field] : b[field].toString();

            if (!isNaN(valueA) && !isNaN(valueB)) {
                return isAscending ? Number(valueA) - Number(valueB) : Number(valueB) - Number(valueA);
            } else {
                return isAscending ? valueA.localeCompare(valueB) : valueB.localeCompare(valueA);
            }
        });

        this.setState({
            carList: sortedList,
            [field]: !isAscending //   
        })}
        else
        {
        }
    }

    render() {
        const { selectedRow, carList, confirmationVisible, model, brand } = this.state;
        return (<div>
            <header className="header-pr">
                <button className="acc-vis" onClick={this.openModal}>Register new transport</button>
            </header>
                <h1 className="car-text">Transport List</h1>
            <div className="content-container-supply">
                <div className="table-container-supply">
            <table className="bg-rg">
                <thead>
                <tr>
                    {brand ?
                        <th className="table-label-pr-cursor" onClick={() => this.sortData('brand')}>Brand</th>
                        :
                        <th className="table-label-pr-cursor" onClick={() => this.sortData('brand')}>Brand</th>
                    }
                    {model ?
                        <th className="table-label-pr-cursor" onClick={() => this.sortData('model')}>Model</th>
                        :
                        <th className="table-label-pr-cursor" onClick={() => this.sortData('model')}>Model</th>
                    }

                    <th className="table-label-pr">Remaining Fuel</th>
                    <th className="table-label-pr">Maximum Fuel</th>
                    <th className="table-label-pr">Condition</th>
                    <th className="table-label-pr">In Operation</th>
                    <th className="table-label-pr"></th>
                </tr>
                </thead>
                <tbody>

                {carList ? (carList.map((car) => (
                            <tr
                                key={car.id}
                                className={car.id === selectedRow ? 'selected-row' : ''}
                                onClick={() => {
                                    console.log(car.id);
                                    this.setState({ selectedRow: car.id }, () => {
                                        console.log(this.state.selectedRow);
                                    });
                                }}
                            >
                        <td className="table-label-pr">{car.brand}</td>
                        <td className="table-label-pr">{car.model}</td>
                        <td className="table-label-pr">{car.remaining_fuel}</td>
                        <td className="table-label-pr">{car.maximum_fuel}</td>
                        <td className="table-label-pr">{car.condition}</td>
                        <td className="table-label-pr">{car.inOperation ? 'Yes' : 'No'}</td>
                        <td className="table-label-pr">
                            {car.inOperation ?
                            <td className="table-label-pr">
                                <button className="fuel-but"
                                        onClick={() => this.handleAction(car.id, this.retire, `Are you sure retire this transport?`)}>
                                    <img src={retire} className="fuel" alt=" button"/>
                                </button>
                                <div className="tooltip" id="tooltip">Retire</div>
                            </td>
                            :
                            <td className="table-label-pr">
                                <button className="fuel-but"
                                        onClick={() => this.handleAction(car.id, this.rehab, `Are you sure rehabilitate this transport?`)}>
                                    <img src={rehab} className="fuel" alt=" button"/>
                                </button>
                                <div className="tooltip" id="tooltip">Rehabilitate</div>
                            </td>}
                            <td className="table-label-pr">
                                <button className="fuel-but" onClick={this.openModal_2}>
                                    <img src={fuel} className="fuel" alt=" button"/>
                                </button>
                                <div className="tooltip" id="tooltip">Refuel</div>
                            </td>
                            <td className="table-label-pr">
                                <button className="fuel-but"
                                        onClick={() => this.handleAction(car.id, this.del, `Are you sure delete this transport?`)}>
                                    <img src={close} className="fuel" alt=" button"/>
                                </button>
                                    <div className="tooltip" id="tooltip">Delete</div>
                            </td>
                        </td>
                    </tr>
                ))
                    )
                    :
                    (
                        <tr>
                            <td colSpan="7" className="table-label-pr">No transport data available</td>
                        </tr>
                    )
                }
                </tbody>
            </table>
            </div>
                {this.state.showModal_2 && <Refuel reff={this.state.refuel} amm={false} onClose={this.closeModal_2} idTr={this.state.selectedRow} onRenew={this.fetchTransport}/>}
                {this.state.showModal && <RegTransport onClose={this.closeModal} onRenew={this.fetchTransport} />}
            </div>
            {confirmationVisible &&
                <ModalQuery updateQuery={this.queryAnswer}
                            query={this.state.query}
                            confirm = {true}
                />
            }
            </div>
        );
    }
}

export default TableTransport;
