import React, { Component } from 'react';
import UpdatePrecogs from "./UpdatePrecogs";
import RegPrecogs from "../Technic/RegPrecogs";
import update from "../../img/correct.png";
import close from "../../img/close.png";
import retire from "../../img/retirePr.png"
import rehab from "../../img/rehabPr.png"
import whiteMan from "../../img/white_man.png"
import redMan from "../../img/red_man.png"
import retireMan from "../../img/retire_man.png"
import {baseUrl} from "../../App";
import ModalQuery from "../ModalQuery";

class PrecogsList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            showModal_2: false,
            psychics: null,
            selectedPsychic: null,
            serotoninLevel: 0,
            dopamineLevel: 0,
            stressLevel: 0,
            url:'',
            confirmationVisible: false

        };
    }

    openModal = () => {
        this.setState({ showModal: true });
    };

    closeModal = () => {
        this.setState({showModal: false});
        this.fetchPsychics();
    };

    openModal_2 = () => {
        this.setState({ showModal_2: true });
    };

    closeModal_2 = () => {
        const token = localStorage.getItem('jwtToken');
        this.setState({showModal_2: false});
        this.fetchRenew(`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}`,token, 'GET');
    };

    componentDidMount() {
        this.fetchPsychics();
    }


    fetchPsychics = () => {
        const token = localStorage.getItem('jwtToken');
        fetch(`${baseUrl}/api/v1/precogs`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`,
            },
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }
                return response.json();
            })
            .then(data => {
                this.setState({ psychics: data });
                data!==null && this.setState({ selectedPsychic: data[0] });
            })
            .catch(error => {
                console.error('    :', error);
            });
    };

    handlePsychicChange = (event) => {
        const selectedId = parseInt(event.target.value, 10);
        const selectedPsychic = this.state.psychics.find(psychic => psychic.id === selectedId);
        this.setState({ selectedPsychic });
    };

    handleNumberChange = (fieldName, event) => {
        const medic = fieldName==="stressLevel" ? this.state.selectedPsychic[fieldName]:(100 - this.state.selectedPsychic[fieldName])
        console.log(medic)
        parseInt(event.target.value, 10) <= medic &&
        parseInt(event.target.value, 10) >= 0 &&
        this.setState({ [fieldName]: parseInt(event.target.value, 10) });
    };

    handleButtonClick = (fieldName) => {
        const token = localStorage.getItem('jwtToken');
        const FormData = require('form-data');
        const form = new FormData();
        form.append(`amount`, this.state[fieldName]);
            if(fieldName==="serotoninLevel")
            {   this.fetchChange(token,`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}/enterserotonin`,form)
                this.setState({serotoninLevel: 0})}
            if(fieldName==="dopamineLevel")
            {   this.fetchChange(token,`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}/enterdopamine`,form)
                this.setState({dopamineLevel: 0})}
            if(fieldName==="stressLevel")
            {   this.fetchChange(token,`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}/enterdepressant`,form)
                this.setState({ stressLevel: 0})}
    };

    fetchChange = (token, url, form) =>
    {
        console.log(url)
        fetch(url, {
            method: 'PUT',
            headers: {
                'Authorization': `Bearer ${token}`,
            },
            body: form,
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }

                //  response.text(),      JSON
                return response.text();
            })
            .then(data => {
                console.log(data);
                this.fetchRenew(`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}`,token, 'GET')
            })
            .catch(error => {
                console.error('    :', error);
            });

    }

    fetchRetire = () =>
    {
        const token = localStorage.getItem('jwtToken');
        this.fetchRenew(`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}/retire`,token, 'POST')

    }

    fetchRehabilitate = () =>
    {
        const token = localStorage.getItem('jwtToken');
        this.fetchRenew(`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}/rehabilitate`,token, 'POST')

    }

    fetchDel = (answer) =>
    {
        const token = localStorage.getItem('jwtToken');

        if (answer === 'Yes') {
        this.fetchRenew(`${baseUrl}/api/v1/precogs/${this.state.selectedPsychic.id}`,token, 'DELETE')}
        else
        {
            this.setState({confirmationVisible:false})
        }

    }

    fetchRenew = (url, token, method) =>
    {
        console.log(url)

        fetch(url, {
            method: `${method}`,
            headers: {
                'Authorization': `Bearer ${token}`,
            },
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }

                //  response.text(),      JSON
                if (method==="DELETE"){
                    return response.text();
                }
                else {
                    return response.json();
                }
            })
            .then(data => {
                console.log(data);
                if (method==="DELETE")
                {this.setState({selectedPsychic:null},
                    ()=>{
                        this.setState({confirmationVisible:false})
                    });
                }
                else {this.setState({selectedPsychic:data});}
            })
            .catch(error => {
                console.error('    :', error);
            });

    }



    render() {
        const { selectedPsychic, psychics, confirmationVisible } = this.state;
        return (<div>
                <header className="header-pr">
                    {this.state.showModal && <RegPrecogs onClose={this.closeModal} onRenew={this.fetchPsychics} />}
                    {this.state.showModal_2 && <UpdatePrecogs onClose={this.closeModal_2} pre={this.state.selectedPsychic} />}
                    <button className="new-precog" onClick={this.openModal}>New Precog</button>
                </header>
                <table className="bg-rg-edit">
                    <tbody>
                    <tr className="table-label-pr">
                        <td>
                            {(  selectedPsychic &&
                                selectedPsychic.work &&
                                selectedPsychic.serotoninLevel>50 &&
                                selectedPsychic.stressLevel<50 &&
                                selectedPsychic.dopamineLevel>50) ?
                                <img src={whiteMan} className="fuel-img" alt=" button"/>
                            :
                                (selectedPsychic &&
                                !selectedPsychic.work &&
                                selectedPsychic.serotoninLevel>50 &&
                                selectedPsychic.stressLevel<50 &&
                                selectedPsychic.dopamineLevel>50) ?
                                <img src={retireMan} className="fuel-img" alt=" button"/>:
                                <img src={redMan} className="fuel-img" alt=" button"/>
                            }
                        </td>
                        <td rowSpan='2'>
                            <label className="h-style">Precogs</label>
                            <select id="psychicDropdown" onChange={this.handlePsychicChange}>

                                {psychics ? this.state.psychics.map(psychic => (
                                        <option key={psychic.id} value={psychic.id}>{psychic.preCogName}</option>
                                    ))
                                    : <option value={null}>Registration precog</option>}
                            </select>
                            {selectedPsychic && this.state.selectedPsychic && (
                                <div className='content-container-precog'>
                                    <table className="bg-rg">
                                        <thead>
                                        <tr>
                                        <th colSpan='2'>
                                            <h4 className="h-style">{this.state.selectedPsychic.preCogName}</h4>
                                        </th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr>
                                            <td className="table-label">ID:</td>
                                            <td className="table-label">{this.state.selectedPsychic.id}</td>
                                        </tr>
                                        <tr>
                                            <td className="table-label">Age:</td>
                                            <td className="table-label">{this.state.selectedPsychic.age}</td>
                                        </tr>
                                        <tr>
                                            <td className="table-label">Level dopamine:</td>
                                            <td className="table-label">{this.state.selectedPsychic.dopamineLevel}</td>
                                        </tr>
                                        <tr>
                                            <td className="table-label">Level serotonin:</td>
                                            <td className="table-label">{this.state.selectedPsychic.serotoninLevel}</td>
                                        </tr>
                                        <tr>
                                            <td className="table-label">Stress level:</td>
                                            <td className="table-label">{this.state.selectedPsychic.stressLevel}</td>
                                        </tr>
                                        <tr>
                                            <td className="table-label">Date of comission:</td>
                                            <td className="table-label">{this.state.selectedPsychic.commissionedOn}</td>
                                        </tr>
                                        <tr>
                                            <td className="table-label">On Working:</td>
                                            <td className="table-label">{this.state.selectedPsychic.work ? 'Yes' : 'No'}</td>
                                        </tr>
                                        <tr className="special">
                                            <td className="table-label" colSpan='2'>
                                                {selectedPsychic.work ?
                                                    <td className="table-label-pr">
                                                        <button className="fuel-but" onClick={this.fetchRetire}>
                                                            <img src={retire} className="fuel" alt=" button"/>
                                                        </button>
                                                        <div className="tooltip" id="tooltip">Retire</div>
                                                    </td>
                                                    :
                                                    <td className="table-label-pr">
                                                        <button className="fuel-but" onClick={this.fetchRehabilitate}>
                                                            <img src={rehab} className="fuel" alt=" button"/>
                                                        </button>
                                                        <div className="tooltip" id="tooltip">Rehabilitate</div>
                                                    </td>}
                                                <td className="table-label-pr">
                                                    <button className="fuel-but" onClick={this.openModal_2}>
                                                        <img src={update} className="fuel" alt=" button"/>
                                                    </button>
                                                    <div className="tooltip" id="tooltip">Update</div>
                                                </td>
                                                <td className="table-label-pr">
                                                    <button className="fuel-but" onClick={()=>{
                                                        this.setState({confirmationVisible:true})
                                                    }}>
                                                        <img src={close} className="fuel" alt=" button"/>
                                                    </button>
                                                    <div className="tooltip" id="tooltip">Delete</div>
                                                </td>
                                            </td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                            )}
                        </td>
                    </tr>
                    <tr>
                        <td className="table-label-prr">
                            {this.state.selectedPsychic && (
                                    <table className="bg-rg">
                                        <tbody>
                                        <tr>
                                            <td className='table-label-edit'>
                                                Serotonin
                                            </td>
                                            <td className='table-label-edit'>
                                                <input
                                                    type="number"
                                                    id="serotoninLevel"
                                                    value={this.state.serotoninLevel}
                                                    onChange={(event) => this.handleNumberChange('serotoninLevel', event)}
                                                />
                                            </td>
                                            <td className='table-label-edit'>
                                                <button
                                                    className="buttonStyle"
                                                    onClick={() => this.handleButtonClick('serotoninLevel')}>
                                                    Enter
                                                </button>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td className='table-label-edit'>
                                                Dopamine
                                            </td>
                                            <td className='table-label-edit'>
                                                <input
                                                    type="number"
                                                    id="dopamineLevel"
                                                    className="inputStyle"
                                                    value={this.state.dopamineLevel}
                                                    onChange={(event) => this.handleNumberChange('dopamineLevel', event)}
                                                />
                                            </td>
                                            <td className='table-label-edit'>
                                                <button
                                                    className="buttonStyle"
                                                    onClick={() => this.handleButtonClick('dopamineLevel')}>
                                                    Enter
                                                </button>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td className='table-label-edit'>
                                                Depressant
                                            </td>
                                            <td className='table-label-edit'>
                                                <input
                                                    type="number"
                                                    id="stressLevel"
                                                    className="inputStyle"
                                                    value={this.state.stressLevel}
                                                    onChange={(event) => this.handleNumberChange('stressLevel', event)}
                                                />
                                            </td>
                                            <td className='table-label-edit'>
                                                <button
                                                    className="buttonStyle"
                                                    onClick={() => this.handleButtonClick('stressLevel')}>
                                                    Enter
                                                </button>
                                            </td>
                                        </tr>
                                        </tbody>
                                    </table>
                            )}
                        </td>
                    </tr>
                    </tbody>
                </table>
                {confirmationVisible && (
                    <ModalQuery updateQuery={this.fetchDel}
                                query='Are you sure delete this precog?'
                                confirm = {true}
                    />
                )}
            </div>
        );
    }
}

export default PrecogsList;
