import React, { Component } from 'react';
import {baseUrl} from "../../App";
import ModalQuery from "../ModalQuery";
class CorMans extends Component {
    constructor(props) {
        super(props);
        this.state = {
            idd: 0,
            memberName: this.props.manData.memberName,
            telegramId: this.props.manData.telegramId,
            confirmationVisible:false
        };
    }

    handleInputChange = (event) => {
        const { name, value } = event.target;
        this.setState({ [name]: value });
    };

    closeQuery = (e) =>{
        this.setState({confirmationVisible:false})
    }

    handleUpdateManData = () => {
        const { memberName, telegramId } = this.state;

        const telId = parseInt(telegramId, 10);
        const token = localStorage.getItem('jwtToken');
        const url = `${baseUrl}/api/v1/reactiongroup/${this.props.manData.id}`;

        const requestData = {
            memberName: memberName,
            telegramId: telId,
        };
        if(memberName !== '' && telId !== 0 )
        {
        fetch(url, {
            method: 'PUT',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(requestData),
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error('   ');
                }
                console.log('  ');
                this.props.onRenew();
            })
            .catch(error => {
                console.error('    :', error);
            });

        this.props.onClose();
        }
        else
        {
            this.setState({confirmationVisible:true})
        }
    };

    render() {
        const { onClose } = this.props;
        const { memberName, telegramId, confirmationVisible } = this.state;

        return (
            <div>
                <div className="modal">
                    <div className="modal-content-precog">
                        <span className="close" onClick={onClose}>&times;</span>
                        <h2 className="h-style">Update Men Data</h2>
                        <form className="form-tr">
                            <table className="bg-rg">
                                <tbody>
                                <tr>
                                    <td className="table-label-pr">Member Name:</td>
                                    <td className="table-label-edit">
                                        <input
                                            type="text"
                                            name="memberName"
                                            value={memberName}
                                            onChange={this.handleInputChange}
                                        />
                                    </td>
                                </tr>
                                <tr>
                                    <td className="table-label-pr">Telegram ID:</td>
                                    <td className="table-label-edit">
                                        <input
                                            type="text"
                                            name="telegramId"
                                            value={telegramId}
                                            onChange={this.handleInputChange}
                                        />
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                            <br />
                            <button
                                className="button-tr"
                                type="button"
                                onClick={this.handleUpdateManData}>
                                Update
                            </button>
                        </form>
                    </div>
                </div>
                {confirmationVisible && (
                    <ModalQuery updateQuery={this.closeQuery}
                                query="Member name shouldn't be empty"
                                confirm = {false}
                    />
                )}
            </div>
        );
    }
}

export default CorMans;
