import React from "react";
import Header from "../Header";
import ListCards from "../CrimeCard/ListCards";
import BurgerMenu from "../BurgerMenu";
import CreateCard from "../CrimeCard/CreateCard";
import Criminals from "./Criminals";
import {baseUrl} from "../../App";
class DetectiveMain extends React.Component {
    constructor(props) {

        super(props);
        this.state = {
            showModal: false,
            showModal_2:false,
            showBurger: false,
            showCard:false,
            cards:null,
            visions:null,
            creationDate:false,
            crimeTime:false,
            weapon:false,
            criminalName:false,
            victimName:false
        }

    }

    openModal = () => {
        this.setState({ showModal: true });
        this.setState({ showModal_2: false })
        const token = localStorage.getItem('jwtToken');

        fetch(`${baseUrl}/api/v1/cards`, {
            method: 'GET', //   
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`, //    
            },
        })
            .then(responses => responses.json())
            .then(data => {
                this.setState({cards:data})
            })
            .catch(error => {
                console.error('    :', error);
            });
    };

    closeModal = () => {
        this.setState({showModal: false});
        this.props.renew();
    };

    CreateCarted = (e) => {
        this.setState({showCard: e})
        this.fetchVisions()
    }

    CloseCarted = () => {
        this.setState({showCard: false})
    }


    openCriminals = () => {
        this.setState({showModal_2: true})
        this.setState({showModal: false})
    }

    closeBurger = () => {
        this.setState({showBurger: false});
        this.props.renew();
    };

    fetchVisions = () => {
        const token = localStorage.getItem('jwtToken');
        fetch(`${baseUrl}/api/v1/visions`, {
            method: 'GET', //   
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`, //    
            },
        })
            .then(responses => responses.json())
            .then(data => {
                data ? this.setState({ visions: data }):
                    this.setState({ visions: null });
                console.log("VIS: "+this.state.visions)
            })
            .catch(error => {
                console.error('    :', error);
            });
    };

    sortData = (field) => {
        const isAscending = this.state[field] || false;
        if (this.state.cards){
        const sortedList = this.state.cards.sort((a, b) => {
            const valueA = typeof a[field] === 'string' ? a[field] : a[field].toString();
            const valueB = typeof b[field] === 'string' ? b[field] : b[field].toString();

            if (!isNaN(valueA) && !isNaN(valueB)) {
                return isAscending ? Number(valueA) - Number(valueB) : Number(valueB) - Number(valueA);
            } else {
                return isAscending ? valueA.localeCompare(valueB) : valueB.localeCompare(valueA);
            }
        });

        this.setState({
            cards: sortedList,
            [field]: !isAscending
        })}
        else
        {
            this.setState({cards:null})
        }
    }

    sortDataByDate = (field) => {
        console.log("Sorting by", field);
        const parseDate = (dateString) => {
            const [day, month, year, time] = dateString.split(/[\s-]+/);
            const [hours, minutes, seconds] = time.split(':');
            return new Date(year, month - 1, day, hours, minutes, seconds);
        };
        const isAscending = this.state[field] || false;
        if (this.state.cards){
        const sortedList = [...this.state.cards].sort((a, b) => {
            const dateA = a[field] ? parseDate(a[field]) : null;
            const dateB = b[field] ? parseDate(b[field]) : null;

            if (!dateA || !dateB) {
                return isAscending ? (dateA ? -1 : 1) : (dateB ? -1 : 1);
            }
            console.log('dateA '+dateA+' '+'dateB '+dateB+' ')
            return isAscending ? dateA.getTime() - dateB.getTime() : dateB.getTime() - dateA.getTime();
        });

        this.setState(prevState => ({
            cards: sortedList,
            [field]: !isAscending
        }), () => {
            console.log(this.state.cards); //  
        })}
        else
        {
        this.setState({cards:null})
        }
    }


    render() {
        const {me} = this.props
        const visibleRoles = ["DETECTIVE", "AUDITOR", "REACTIONGROUP","TECHNIC"];
        const rolesToDisplay = visibleRoles.filter(role => me.roles.includes(role));
        return (<div>
                <Header isLogged={this.props.isLogged} me={me}/>
                <BurgerMenu onClose={this.closeBurger} roles={rolesToDisplay} updatePull={this.props.pullRole}/>

                {this.state.showCard && (
                    <div>
                        <CreateCard onClose={this.CloseCarted} visions={this.state.visions}/>
                    </div>
                )}




                <div className="frame-2">
                    <div className="rectangle-2">
                        {this.state.showModal && (
                                <ListCards criminalName = {this.state.criminalName}
                                           victimName = {this.state.victimName}
                                           weapon = {this.state.weapon}
                                           sortData={this.sortDataByDate}
                                           sortABC={this.sortData}
                                           crimeTime={this.state.crimeTime}
                                           creationDate={this.state.creationDate}
                                           onClose={this.closeModal}
                                           crimeList={this.state.cards}
                                           onRenew={this.openModal}
                                           role={"DETECTIVE"}/>
                        )}
                        {this.state.showModal_2 && (
                            <div>
                                <Criminals onClose={this.closeModal}
                                           crimeList={this.state.cards}
                                           onRenew={this.openModal}
                                           role={"DETECTIVE"}/>
                            </div>
                        )}
                    </div>
                </div>
                <div className="frame-but">
                    <div className="rectangle-but" >
                        <button className="card-list" onClick={this.openModal}>Card List</button>
                        <button onClick={()=>{this.CreateCarted(true)}} className="create-card">Create Crime Card</button>
                        <button className="card-list" onClick={this.openCriminals}>Criminals</button>
                    </div>
                </div>
                </div>
        )
    }
}

export default DetectiveMain