// Import React and other necessary modules
import React, { Component } from 'react';
import CrimeCard from "./CrimeCard";
import "../../css/CrimeTable.css"
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


class ListCards extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedRow: null,
            clickCrime: null,
            isFormOpen: false,
            startDate: new Date("01-01-2023 00:00:00"),
            endDate: new Date("01-01-2032 00:00:00"),
            showModal: false,
        };
    }

    handleRowClick = (index) => {
        this.setState({ selectedRow: index }, () => {
            this.setState({ clickCrime: this.props.crimeList.find((crime) => crime.id === index) });
        });
    };


    handleRowDoubleClick = (index) => {
        this.handleRowClick(index);
        this.setState({ isFormOpen: true });
    };

    closeForm = () => {
        this.setState({ isFormOpen: false });
    };



    render() {
        const { crimeList, role, creationDate, sortData, crimeTime, weapon, sortABC, criminalName, victimName } = this.props;
        const { selectedRow, clickCrime, isFormOpen } = this.state;

        return (<div>
            <div className="crime-content-container">
                    <h2 className="h-style">Card List</h2>
                <div className="crime-table-container">
                    <table className="bg-rg">
                        <thead>
                        <tr>
                            {victimName ?
                                <th className="table-label-pr-cursor" onClick={() => sortABC('victimName')}>Victim Name</th>
                                :
                                <th className="table-label-pr-cursor" onClick={() => sortABC('victimName')}>Victim Name</th>
                            }
                            {criminalName ?
                                <th className="table-label-pr-cursor" onClick={() => sortABC('criminalName')}>Criminal Name</th>
                                :
                                <th className="table-label-pr-cursor" onClick={() => sortABC('criminalName')}>Criminal Name</th>
                            }
                            {weapon ?
                                <th className="table-label-pr-cursor" onClick={() => sortABC('weapon')}>Weapon</th>
                                :
                                <th className="table-label-pr-cursor" onClick={() => sortABC('weapon')}>Weapon</th>
                            }
                            {creationDate ?
                                <th className="table-label-pr-cursor" onClick={() => sortData('creationDate')}>
                                    Creation Date
                                </th>
                                :
                                <th className="table-label-pr-cursor" onClick={() => sortData('creationDate')}>Creation Date</th>
                            }
                            {crimeTime ?
                                <th className="table-label-pr-cursor" onClick={() => sortData('crimeTime')}>
                                    Crime Time
                                </th>
                                :
                                <th className="table-label-pr-cursor" onClick={() => sortData('crimeTime')}>Crime Time</th>
                            }
                            {role === 'AUDITOR' &&
                                <th className="table-label-pr">Detective</th>
                            }
                        </tr>
                        </thead>
                        <tbody>
                        {crimeList && crimeList.length > 0 ? (
                            crimeList.map((crime) => (
                                   <tr
                                        key={crime.id}
                                        className={crime.id === selectedRow ? 'selected-row' : ''}
                                        onClick={() => this.handleRowClick(crime.id)}
                                        onDoubleClick={() => this.handleRowDoubleClick(crime.id)}
                                    >
                                        <td className="table-label-pr">{crime.victimName}</td>
                                        <td className="table-label-pr">{crime.criminalName}</td>
                                        <td className="table-label-pr">{crime.weapon}</td>
                                        <td className="table-label-pr">{crime.creationDate}</td>
                                       <td className="table-label-pr">{crime.crimeTime}</td>
                                        {role === 'AUDITOR' && (
                                            <td className="table-label-pr">{crime.responsibleDetective}</td>
                                        )}
                                    </tr>
                                )))
                                :(
                            <tr>
                                <td colSpan="5" className="table-label-pr">No crime data available</td>
                            </tr>
                        )}
                        </tbody>
                    </table>
                </div>
                {clickCrime && isFormOpen && (
                    <div className="crime-card-container">
                        <CrimeCard crimeCard={clickCrime} onClose={this.closeForm} onRenew={this.props.onRenew} role={this.props.role}/>
                    </div>
                )}
            </div>
            </div>
        );
    }
}

//  
export default ListCards;
