import React, { Component } from 'react';
import {baseUrl} from "../../App";
import ModalQuery from "../ModalQuery";
class UpdatePrecogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            idd: 0,
            preCogName:this.props.pre.preCogName,
            age:this.props.pre.age,
            confirmationVisible:false
        };
    }

    handleInputChange = (event) => {
        const { name, value } = event.target;
        (name === "age") && (value>=0) && value<1000 && this.setState({ [name]: value });
        (name === "preCogName") && this.setState({ [name]: value });
    };

    handleSubmit = () => {
        const token = localStorage.getItem('jwtToken');
        const url = `${baseUrl}/api/v1/precogs/${this.props.pre.id}`;
        if(this.state.preCogName !== '' && this.state.age !== 0 )
        {fetch(url, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`,
            },
            body:JSON.stringify({
                preCogName:this.state.preCogName,
                age: this.state.age
            })
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error('   ');
                }
                console.log('  ');
                this.props.onClose();
            })
            .catch(error => {
                console.error('    :', error);
            })}
        else
        {
            this.setState({confirmationVisible:true})
        }

    };

    closeQuery = (e) =>{
        this.setState({confirmationVisible:false})
    }
    render() {
        const { onClose } = this.props;
        const {  preCogName, age, confirmationVisible } = this.state;

        return (<div>
                <div className="modal">
                    <div className="modal-content-precog">
                        <span className="close" onClick={onClose}>&times;</span>
                        <h2 className="h-style">Update Precog</h2>
                        <form className="form-tr">
                            <table className="bg-rg">
                                <tbody>
                                <tr>
                                    <td className="table-label-edit">PreCog Name:</td>
                                    <td className="table-label-edit">
                                        <input maxLength="20" type="text" name="preCogName" value={preCogName} onChange={this.handleInputChange} />
                                    </td>
                                </tr>
                                <tr>
                                    <td className="table-label-edit">Age:</td>
                                    <td className="table-label-edit">
                                        <input type="number" name="age" value={age} onChange={this.handleInputChange} />
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                            <br/>
                            <button className="button-tr" type="button" onClick={this.handleSubmit}>Submit</button>
                        </form>
                    </div>
                </div>
                {confirmationVisible && (
                    <ModalQuery updateQuery={this.closeQuery}
                                query="Input all fields"
                                confirm = {false}
                    />
                )}
            </div>
        );
    }
}

export default UpdatePrecogs;
