import React from "react";
class ModalQuery extends React.Component {
    constructor(props) {
        super(props);
    }


    render() {
        return ( <div className="modal">
                <div className="modal-content-precog">
                    <h2 className="h-style">{this.props.query}</h2>
                    <form className="form-tr">
                        <table className="bg-rg">
                            <tbody>
                            {this.props.confirm ?
                            <tr>
                                <td className="table-label-edit">
                                    <button type="button" onClick={()=>{
                                    this.props.updateQuery('Yes')
                                    }}>
                                        Yes
                                    </button>
                                </td>
                                <td className="table-label-edit">
                                    <button type="button" onClick={()=>{
                                        this.props.updateQuery('No')
                                    }}>
                                        No
                                    </button>
                                </td>
                            </tr>:
                            <tr>
                                 <td className="table-label-edit">
                                    <button type="button" onClick={()=>{
                                        this.props.updateQuery('OK')
                                    }}>
                                        OK
                                    </button>
                                </td>
                            </tr>
                            }
                            </tbody>
                        </table>
                    </form>
                </div>
            </div>
        )
    }
}

export default ModalQuery