import React, { Component } from 'react';
import accept from "../../img/accept.png";
import close from "../../img/close.png";
import {baseUrl} from "../../App";
import ModalQuery from "../ModalQuery";
class TableVision extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedRow: null,
            selectedVideoUrl: null,
            visionList: null,
            confirmationVisible:false,
        };
    }


    handleRowClick = (index, url) => {
        this.setState({ selectedRow: index }, ()=>{
            this.setState({selectedVideoUrl:url})
            this.props.updateId(index)
            console.log(url)
        });

    };

    componentDidMount() {
        this.fetchVision()
    }

    fetchDel = (answer) =>
    {

        if (answer === 'Yes') {
            this.del()}
        else
        {
            this.setState({confirmationVisible:false})
        }

    }

    fetchVision = () => {
        const token = localStorage.getItem('jwtToken');

        const handleApiResponse = async (response, property) => {
            if (response.ok) {
                try {
                    return await response.json();
                } catch (error) {
                    console.error('Error parsing JSON:', error);
                    return null;
                }
            } else {
                console.error(`Error fetching data: ${response.status}`);
                return null;
            }
        };

        Promise.all([
            fetch(`${baseUrl}/api/v1/visions`, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`,
                },
            }).then(response => handleApiResponse(response, 'visionList')),

            fetch(`${baseUrl}/api/v1/visions/used`, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`,
                },
            }).then(response => handleApiResponse(response, 'visionList')),
        ]).then(dataArray => {
            const mergedData = dataArray.reduce((acc, data) => (data ? [...acc, ...data] : acc), []);
            this.setState({ visionList: mergedData }, ()=>{this.setState({selectedVideoUrl:null})});
        }).catch(error => {
            console.error('Error fetching data:', error);
            this.setState({ visionList: null });
        });
    }

    del = () => {
        const token = localStorage.getItem('jwtToken');
        const url = `${baseUrl}/api/v1/visions/${this.state.selectedRow}`;
         {
            fetch(url, {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`,
                },
            })
                .then(response => {
                    if (!response.ok) {
                        throw new Error(response.status);
                    }
                    console.log('  ');
                    this.setState({confirmationVisible:false})
                    this.fetchVision()
                })
                .catch(error => {
                    console.log(error.message)
                    if (error.message === "500") {
                        window.alert("This vision is already in use");
                    } else {
                        window.alert("You haven't chosen a vision");
                    }
                });
            this.fetchVision()
        }
    }

    acceptVision = () => {
        const token = localStorage.getItem('jwtToken');
            fetch(`${baseUrl}/api/v1/visions/${this.state.selectedRow}/accept`, {
                method: 'POST', //   
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`, //    
                },
                body: JSON.stringify({
                }),
            })
                .then(response => response.json())
                .then(data => {
                    this.setState({vision: data}, ()=>{this.fetchVision()});

                })
                .catch(error => {
                    this.fetchVision()
                })
            this.fetchVision()

    };


    handleAction = (carId, actionFunction) => {
        if (carId !== this.state.selectedRow) {
            this.setState({ selectedRow: carId }, () => {
                actionFunction(carId);
            });
        } else {
            actionFunction(carId);
        }
    }

    renderTooltipButton(id, actionFunction, imageSrc, tooltipText) {
        return (
            <td className="table-label-pr">
                <button
                    className="fuel-but"
                    onClick={() => this.handleAction(id, actionFunction)}
                >
                    <img src={imageSrc} className="fuel" alt=" button" />
                </button>
                <div className="tooltip" id="tooltip">{tooltipText}</div>
            </td>
        );
    }

    render() {
        const { selectedRow, visionList, selectedVideoUrl, confirmationVisible } = this.state;
        const { selectedOptions } = this.props
        return (
            <table className="bg-rg">
                <tbody>
                <td className="table-label-pr">
            <div className='content-container-vision'>
                    <table className="bg-rg">
                        <thead>
                        <tr>
                            <th className="table-label-pr">Video</th>
                            <th className="table-label-pr">Accepted</th>
                            <th className="table-label-pr">Already Used</th>
                            <th className="table-label-pr"></th>
                        </tr>
                        </thead>
                        <tbody>
                        {visionList ? (
                            visionList.map((vision) => (
                                ((selectedOptions.length===0 &&
                                        !vision.accepted)
                                ||
                                (selectedOptions.some((option) => option.label.toLowerCase() === 'accepted') &&
                                        vision.accepted ))
                                &&
                                <tr
                                    key={vision.id}
                                    className={vision.id === selectedRow ? 'selected-row' : ''}
                                    onClick={() => {
                                        console.log(vision.id)
                                        this.handleRowClick(vision.id, vision.videoUrl)
                                    }}
                                >
                                    <td className="table-label-pr">
                                        Vision {vision.id}
                                    </td>
                                    <td className="table-label-pr">{vision.accepted ? 'Yes' : 'No'}</td>
                                    <td className="table-label-pr">{vision.alreadyInUse ? 'Yes': 'No'}</td>
                                    {!vision.accepted ?
                                    <td className="table-label-pr">
                                        {this.renderTooltipButton(vision.id, this.acceptVision, accept, 'Accept')}
                                        {this.renderTooltipButton(vision.id, ()=>{
                                            this.setState({confirmationVisible:true})
                                        }, close, 'Delete')}
                                    </td>:
                                        <td className="table-label-pr">

                                        </td>
                                    }
                                </tr>
                            )))
                            :(
                            <tr>
                                <td className="table-label" colSpan="4">No vision data available</td>
                            </tr>)
                        }
                        </tbody>
                    </table>
            </div>
                </td>
                <td className="table-label-pr">
                    {selectedVideoUrl && (
                        <div className="video-container">
                            <iframe
                                title="Vision Video"
                                width="900"
                                height="800"
                                src={selectedVideoUrl}
                                frameBorder="0"
                                allowFullScreen
                            ></iframe>
                        </div>
                    )
                    }
                </td>

                </tbody>
                {confirmationVisible && (
                    <ModalQuery updateQuery={this.fetchDel}
                                query='Are you sure delete this precog?'
                                confirm = {true}
                    />
                )}
            </table>
        );
    }
}


export default TableVision;
